//*
$font-primary: "Open Sans", sans-serif;
$font-secondary: "Montserrat", sans-serif;
$font-thirdary: "GOST Type BU", sans-serif;
//*  
$font-size:(
    'default':  size(14),
    'lg':       size(16),
);
//*   (body)
$global-settings:(
    'font': $font-primary,
    'line-height': 1.62,
    'font-weight': 400,
    'color': #404040,
    'bg': #ffffff,
);

//*    
$td: 400ms;

