.box,
.box-center,
.box-center-x,
.box-center-y,
.box-reverse,
.box-y,
.box-y-reverse {
  --dp-flex: flex;
  display: var(--dp-flex)
}

.item {
  --dp-block: block;
  display: var(--dp-block)
}

.t\:100 {
  font-weight: 100
}

.t\:200 {
  font-weight: 200
}

.t\:300 {
  font-weight: 300
}

.t\:400 {
  font-weight: 400
}

.t\:500 {
  font-weight: 500
}

.t\:600 {
  font-weight: 600
}

.t\:700 {
  font-weight: 700
}

.t\:800 {
  font-weight: 800
}

.t\:900 {
  font-weight: 900
}

.t\:left {
  text-align: left
}

.t\:right {
  text-align: right
}

.t\:center {
  text-align: center
}

.t\:italic {
  font-style: italic
}

.t\:no-italic {
  font-style: normal
}

.t\:uppercase {
  text-transform: uppercase
}

.t\:transform-none {
  text-transform: none
}

.inline {
  --dp-inline: inline;
  display: var(--dp-inline);
}

.box-reverse {
  flex-direction: row-reverse
}

.box-y {
  flex-direction: column
}

.box-y-reverse {
  flex-direction: column-reverse
}

.box-center {
  --justify: center;
  justify-content: var(--justify)
}

.box-center,
.box-center-y {
  --align: center;
  align-items: var(--align)
}

.box-center-x {
  --justify: center;
  justify-content: var(--justify)
}

.m\:inline {
  --dp-flex: inline-flex;
  --dp-grid: inline-grid;
  --dp-block: inline-block;
  --dp-inline: inline
}

.m\:block {
  --dp-flex: flex;
  --dp-grid: grid;
  --dp-block: block;
  --dp-inline: block
}

.m\:hide,
.m\:show\@lg,
.m\:show\@md {
  --dp-flex: none;
  --dp-grid: none;
  --dp-block: none;
  --dp-inline: none
}



.m\:show {
  --dp-flex: flex;
  --dp-grid: grid;
  --dp-block: block;
  --dp-inline: inline
}

.m\:left {
  margin-right: auto;
  margin-left: 0
}

.m\:left-0,
.m\:right {
  margin-right: 0
}

.m\:right {
  margin-left: auto
}

.m\:right-0 {
  margin-left: 0
}

.m\:top {
  margin-bottom: auto;
  margin-top: 0
}

.m\:bottom,
.m\:top-0 {
  margin-bottom: 0
}

.m\:bottom {
  margin-top: auto
}

.m\:bottom-0 {
  margin-top: 0
}

.m\:center {
  margin: auto
}

.m\:center-0 {
  margin: 0
}

.m\:center-x {
  margin-left: auto;
  margin-right: auto
}

.m\:center-x-0 {
  margin-left: 0;
  margin-right: 0
}

.m\:center-y {
  margin-top: auto;
  margin-bottom: auto
}

.m\:center-y-0 {
  margin-top: 0;
  margin-bottom: 0
}

.m\:relative {
  position: relative
}

.m\:static {
  position: static
}

.m\:nowrap {
  flex-wrap: nowrap
}

.m\:wrap {
  flex-wrap: wrap
}

.m\:wrap-reverse {
  flex-wrap: wrap-reverse
}

